import React, {useState} from "react";
import style from "./about_as_style.module.css";
import {Map, Placemark, YMaps} from "react-yandex-maps";

import icon1 from '../../img/icon/list.svg'
import icon2 from '../../img/icon/production.svg'
import icon3 from '../../img/icon/quality.svg'

const mapData = {
    center: [55.751574, 37.573856],
    zoom: 5,
};

const coordinates = [
    [55.684758, 37.738521],
    [57.684758, 39.738521]
];

function About_Us() {


    return (
        <div className={style.content}>
            <h2> </h2>
            <div className={style.map}>
                <YMaps>
                    <Map width={'99%'} height={'400px'} defaultState={mapData}>
                        {coordinates.map(coordinate => <Placemark geometry={coordinate}/>)}
                    </Map>
                </YMaps>
            </div>
            <div className={style.info}>
                <h2>   </h2>
                <div className={style.address}>
                    <div>
                        <h5>. , .  , . 61</h5>
                        <h6> :</h6>
                        <ul>
                            <li>: 8:00 - 20:00</li>
                            <li>: 8:00 - 20:00</li>
                            <li>: 8:00 - 20:00</li>
                            <li>: 8:00 - 20:00</li>
                            <li>: 8:00 - 20:00</li>
                            <li>: 10:00 - 18:00</li>
                            <li>: 10:00 - 18:00</li>
                        </ul>
                    </div>
                    <div>
                        <h5>. , . , . 25</h5>
                        <h6> :</h6>
                        <ul>
                            <li>: 8:00 - 20:00</li>
                            <li>: 8:00 - 20:00</li>
                            <li>: 8:00 - 20:00</li>
                            <li>: 8:00 - 20:00</li>
                            <li>: 8:00 - 20:00</li>
                            <li>: 10:00 - 18:00</li>
                            <li>: 10:00 - 18:00</li>
                        </ul>
                    </div>
                </div>

            </div>
            <div className={style.list}>
                <div className={style.list_item}>
                    <div className={style.title_list}>
                        <img src={icon3} alt=""/>
                        <h3> </h3>
                    </div>
                    <p>        -.</p>
                </div>
                <div className={style.list_item}>
                    <div className={style.title_list}>
                        <img src={icon1} alt=""/>
                        <h3> </h3>
                    </div>
                    <p>       ,   . </p>
                </div>
                <div className={style.list_item}>
                    <div className={style.title_list}>
                        <img src={icon2} alt=""/>
                        <h3> </h3>
                    </div>
                    <p>        . ,     !</p>
                </div>
            </div>
        </div>
    );
}

export default About_Us;
